<script>

  import {
    DataTable,
    Toolbar,
    ToolbarContent,
    ToolbarSearch,
    ToolbarMenu,
    ToolbarMenuItem,
    Button
  } from "carbon-components-svelte";

  import { Link, navigate } from "svelte-navigator";
 import Add from "carbon-icons-svelte/lib/Add.svelte";
		import { FormatDate } from './lib/utils';

export let issues;
export let nodeAD;

const renderSeverity = (s) => {
    let sev = parseInt(s,10);
    switch (sev) {
    case 10:
        return "Critical"
    case 9:
        return "High"
    case 8:
        return "Medium"
    case 7:
        return "Low"
    case 6:
        return "Information"
    case 5:
        return "Investigating"
    case 4:
        return "Closed"
    default:
        return "New (TBD)"
    }
    return "-";
};

</script>

<style>
</style>



<DataTable
  sortable
  title="Issues"
  headers={[
    { key: "n1", value: "Severity", width: "15%" },
    { key: "s1", value: "Title", width: "55%" },
    { key: "m", value: "Last Modified", width: "30%" }
    ]}
  rows={issues}
  >

  <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "s1"}
      <Link
        to={"/ui/usr/item-view/"+row.ty+"/"+row.ad}
        >{cell.value || "[Untitled]"}</Link>
    {:else if cell.key === "n1"}
      {renderSeverity(cell.value)}
    {:else if cell.key === "m"}
      {FormatDate(cell.value)}
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>


<Toolbar size="sm">
    <ToolbarContent>
      <ToolbarSearch
        persistent
        shouldFilterRows
        />
        {#if !!nodeAD}
      <Button icon={Add} size="small" kind="tertiary" on:click={() => navigate("/ui/usr/item-add/issue/"+nodeAD)}>New Issue</Button>
      {/if}
    </ToolbarContent>
  </Toolbar>

</DataTable>



