import { Document, Packer, Paragraph, TextRun, ImageRun, HeadingLevel } from "docx";


function getImageDimensions(base64String) {
    return new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = function() {
            resolve({ width: img.width, height: img.height });
        };
        img.onerror = function() {
            reject(new Error("Failed to load image"));
        };
        img.src = base64String;
    });
}

function populateTextrun(n, run) {
    switch (n.nodeName) {
	case "#text":
	    run.text = n.nodeValue;
	    return;
	case "STRONG":
        run.bold = true;
        break;
	case "EM":
        run.italics = true;
        break;
	case "U":
        run.underline = {};
        break;
	case "SPAN":
        if(!!n.className && n.className.indexOf('monospace') > 0) {
            run.font = {name: "Courier New"};
        }
        break;
    }

    if(n.childNodes && n.childNodes.length > 0)
        populateTextrun(n.childNodes[0], run);
    return;
}

async function getImagerun(n) {
    let run = {};
    let srcdata = n.attributes.src.value;
    //todo: regex check to validate inline data
    let parts = srcdata.split(',');
    let metadata = parts[0];
    run.type = metadata.split(';')[0].split('/')[1];
    run.data = parts[1];
    const dimensions = await getImageDimensions(srcdata);
    run.transformation = {
        width: dimensions.width,
        height: dimensions.height
    }
    return run;
}

async function getListitem(n,listparas,depth=0) {
    let para = {
        children: [],
        bullet: {
                level: depth
        }
	};

    let arr = n.childNodes;
    for(let i=0; i < arr.length; i++) {
        let child = arr[i];
        switch(child.nodeName) {
            case "IMG":
                const irdata = await getImagerun(child);
                let ir = new ImageRun(irdata);
                para.children.push(ir);
                break;
            case "OL":
            case "UL":
                listparas.push(para);
                for(let j=0; j < child.childNodes.length; j++)
                    getListitem(child.childNodes[j],listparas,depth+1);
                return;
            default:
                let trdata = {};
                populateTextrun(child,trdata);
                let tr = new TextRun(trdata);
                para.children.push(tr);
        }
    }

    listparas.push(para);
}


async function getParagraphs(n) {
    let para = { children: [] };
    if(!n.innerText && !n.innerHTML)
        return [para];

    switch (n.nodeName) {
	case "P":
	    let arr = n.childNodes;
	    if(arr.length < 1)
            break;
        para.children = [];
        for(let i=0; i < arr.length; i++) {
            let child = arr[i];
            switch(child.nodeName) {
                case "IMG":
                    const irdata = await getImagerun(child);
                    let ir = new ImageRun(irdata);
                    para.children.push(ir);
                    break;
                default:
                    let trdata = {};
                    populateTextrun(child,trdata);
                    let tr = new TextRun(trdata);
                    para.children.push(tr);
            }
        }
            // n.innerText != n.innerHTML
	    break;
    case "PRE":
	    n.innerText.split('\n').forEach(s => para.children.push(new TextRun({text: s, break: 1})));
        para.style = 'codeblock';
        break;
    case "H1":
        para.text = n.innerText;
        para.heading = HeadingLevel.HEADING_1;
        break;
    case "H2":
        para.text = n.innerText;
        para.heading = HeadingLevel.HEADING_2;
        break;
    case "H3":
        para.text = n.innerText;
        para.heading = HeadingLevel.HEADING_3;
        break;
    case "H4":
        para.text = n.innerText;
        para.heading = HeadingLevel.HEADING_4;
        break;
    case "UL":
    case "OL":
        let listparas = [];
	    let arrL = n.childNodes;
	    if(arrL.length < 1)
            break;
        for(let i=0; i < arrL.length; i++) {
            let childL = arrL[i];
            await getListitem(childL,listparas,0);
        }
        return listparas;
    }
    return [para];
}

async function docxParagraphsFromHtmlNode(n) {
    const pdata = await getParagraphs(n);
    let dps = [];
    for(let pi=0; pi < pdata.length; pi++){
        let p = pdata[pi];
    	let dp = new Paragraph(p);
        dps.push(dp);
    }
    return dps;
}

async function htmlToDocx(htmlstring) {
  const dp = new DOMParser();
  let hdom = dp.parseFromString(htmlstring,'text/html');
  let docstructure = {
    styles: {
        paragraphStyles: [
            {
                id: "codeblock",
                name: "codeblock",
                basedOn: "Normal",
                next: "Normal",
                run: {
                    color: "555555",
                    font: "Courier New",
                }
            }
        ]
    },
    sections: [
        {
            properties: {},
            children: []
        }
    ]
   };

  let bodyElem = hdom.children[0].children[1];
  for(let i=0; i < bodyElem.children.length; i++) {
    let docxps = await docxParagraphsFromHtmlNode(bodyElem.children[i]);
    docxps.forEach(p =>
    	docstructure.sections[0].children.push(p)
    );
  }
  return new Document(docstructure);
}


export {
    htmlToDocx
}
