<script>

 //import { DataTable } from "carbon-components-svelte";
  import { Button, TextInput } from "carbon-components-svelte";
	import AsyncWrapper from './AsyncWrapper.svelte';
  import { Link } from "svelte-navigator";



  import {
    DataTable,
    Toolbar,
    ToolbarContent,
    ToolbarSearch,
    ToolbarMenu,
    ToolbarMenuItem,
  } from "carbon-components-svelte";

  import { FormatDate } from './lib/utils';


    export let usr;

    let fetchData = async () => {
      let projdata = await usr.getProjects();
      return projdata.result_nodes;
    }

</script>

<style>
</style>



<div>
	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>

<DataTable
  sortable
  title="Projects"
  description="List of projects you have been assigned to"
  headers={[
    { key: "s1", value: "Title", width: "70%" },
    { key: "c", value: "Last Modified", width: "30%" }
    ]}
  rows={dataFromAwaitable}
  >

  <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "s1"}
      <Link
        to={"/ui/usr/proj-dashboard/"+row.ad}
        >
      {cell.value || "[Untitled]"}
        </Link>
    {:else if cell.key === "c"}
      {FormatDate(cell.value)}
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>


<Toolbar size="sm">
    <ToolbarContent>
      <ToolbarSearch
        persistent
        shouldFilterRows
        />
    </ToolbarContent>
  </Toolbar>

</DataTable>






	</AsyncWrapper>
</div>

