class Usr {
    constructor(cogged) {
        this._c = cogged;
        this.currentUserUid = '';
        cogged.currentUserId.subscribe((value) => {
            this.currentUserUid = value;
        });
        this._usernames = {};
    }


    combineLists(list1, list2) {
        let returnList = [];
        let s = new Set();
        if (!!list1) {
            list1.forEach(e => s.add(e));
        }
        if (!!list2) {
            list2.forEach(e => s.add(e));
        }
        s.forEach(e => returnList.push(e));
        return returnList;
    }

    getMyUserUid() {
        return this.currentUserUid;
    }

    amOwnerOf(node) {
        return !!node && !!node.own && (this.getMyUserUid() == node.own.uid);
    }

    canWrite(node) {
        return !!node.w || this.amOwnerOf(node);
    }

    async getUsernameForUid(uid) {
        let un = this._usernames[uid];
        if(!!un)
            return un;
        let rd = await this._c.userUidGet(uid);
        if(!!rd && !!rd.user && !!rd.user.un) {
             this._usernames[uid] = rd.user.un;
             return rd.user.un;
        }
        return "unknownuser";
    }


    async getProjects() {
        let querydata = {"depth":1, "select":["id","c","s1"], "filters":{"and":[{"field":"m","op":"gt","val":"0"},{"field":"ty","op":"eq","val":"proj"}]}};
        let data = {};
        data = await this._c.userNodesSharedPost(querydata);
        return data;
    }

    async getAllChildNodes(parentAD, select=[]) {
        let selectfields = this.combineLists(["id","ty"],select);
        let querydata = {"depth":1, "select":selectfields, "root_ids":[parentAD],"filters":{"field":"m","op":"gt","val":"0"}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async getChildNodesByType(parentAD, types, select=[]) {
        let selectfields = this.combineLists(["id","ty"],select);
        let orClauses = [];
        types.forEach(t => {
            orClauses.push({"field":"ty","op":"eq","val":t});
        });
        let querydata = {"depth":1, "select":selectfields, "root_ids":[parentAD],"filters":{"or": orClauses}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async getAllChildProjectsAndIssues(rootAD) {
        let orClauses = [
            {"field":"ty","op":"eq","val":"proj"},
            {"field":"ty","op":"eq","val":"issue"}
        ];
        let querydata = {"depth":20, "select":["ty","s1","n1","e","m"], "root_ids":[rootAD],"filters":{"or": orClauses}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async addPage(parentAD, nodedata) {
        nodedata.ty = "page"
        return await this.addNodeData(parentAD, nodedata);
    }

    async addIssue(parentAD, nodedata) {
        nodedata.ty = "issue"
        return await this.addNodeData(parentAD, nodedata);
    }

    async addNodeData(parentAD, nodedata) {
        const n = {"uid":"$1","id":nodedata.id,"ty":nodedata.ty,"s1":nodedata.s1,"r":true,"o":true,"e":[]}
        if(!!nodedata.w) n['w'] = true;
        if(!!nodedata.i) n['i'] = true;
        if(!!nodedata.s) n['s'] = true;
        let requestdata = {"nodes":[n]};
        let data = await this._c.graphNodesPut(parentAD, requestdata);
        return data;
    }

    async addNode(parentAD, newnode) {
        newnode.uid = "$1";
        let requestdata = {"nodes":[newnode]};
        let data = await this._c.graphNodesPut(parentAD, requestdata);
        return data;
    }

    async addProject(parentprojAD, projdata) {
        projdata.uid = "$1";
        projdata.ty = "proj";
        projdata.r = true;
        projdata.o = true;
        let requestdata = {"nodes":[projdata]};
        let data = await this._c.graphNodesPut(parentprojAD, requestdata);
        return data;
    }

    async getHostNodes(projAD) {
        let querydata = {"depth":1, "select":["id","ty","s1","s2","s3","s4"], "root_ids":[projAD],"filters":{"field":"ty","op":"eq","val":"host"}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async getHostPortsAndResults(hostAD) {
        let querydata = {"depth":2, "select":["id","ty","s1","n1","e"], "root_ids":[hostAD],"filters":{"field":"m","op":"gt","val":"0"}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async getPortResults(portAD) {
        let querydata = {"depth":1, "select":["id","ty","s1","n1"], "root_ids":[portAD],"filters":{"field":"m","op":"gt","val":"0"}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async getHostsPorts(hostADList) {
        let querydata = {"depth":1, "select":["id"], "root_ids": hostADList, "filters":{"field":"ty","op":"eq","val":"port"}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async getPageData(nodeAD) {
        return await this.getNodeData(nodeAD, ['s4']);
    }

    async getIssueData(nodeAD) {
        return await this.getNodeData(nodeAD, ['s2','s4','n1']);
    }

    async getNodeData(nodeAD, select=[]) {
        let selectfields = this.combineLists(["id","ty","m","s1"],select);
        let querydata = {"depth":0, "select":selectfields, "root_ids":[nodeAD],"filters":{"field":"m","op":"gt","val":"0"}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async updateNodeData(nodedata) {
        let requestdata = {"nodes":[nodedata]};
        let data = await this._c.graphNodesPatch(requestdata);
        return data;
    }
}


export {
    Usr
}
//export let LoggedIn = writable(true);

