<script>

 import { DataTable } from "carbon-components-svelte";
  import { Search, Button, TextInput } from "carbon-components-svelte";

	import AsyncWrapper from './AsyncWrapper.svelte';
import ToastNotify from "./ToastNotify.svelte";


    export let sys;
    export let adm;
    export let projAD;

    let notification = null;

    let complete = false;
    $: fetchComplete = () => complete;

    let fetchData = async () => {
      let projmembers = await adm.getProjMembers(projAD);
      complete = true;
      let res = projmembers.result_users;
      sys.addDatatableIdsToArray(res);
      return res;
    }

    let s_username = '';
    let d_username = '';
    let d_uid = '';
    let d_role = '';
    let d_data = '';
    let d_ad = '';

    let clickedSearch = async () => {
      try {
        let userdata = await sys.getUserInfo(s_username);
        d_username = userdata.user.un;
        d_uid = userdata.user.uid;
        d_role = userdata.user.role;
        d_data = userdata.user.us;
        d_ad = userdata.user.ad;
      } catch (err) {
        d_username = err.message;
        d_uid = '';
        d_role = '';
        d_data = '';
        d_ad = '';
      }
    }

    let clickedAdd = async (projAD, userAD) => {
      let responsedata = null;
      let errorMessage = "";
      try {
        responsedata = await adm.addProjectMember(projAD, userAD);
      } catch (err) {
        errorMessage = err.message;
      }
      console.log(responsedata);
      if(!!responsedata && !errorMessage) {
        notification = '+'+JSON.stringify(responsedata);
      } else {
        notification = `Failed to add user as project member (${errorMessage})`;
      }

    }

    let clickedRemove = async (projAD, userAD) => {
      let responsedata = null;
      let errorMessage = "";
      try {
        responsedata = await adm.removeProjectMember(projAD, userAD);
      } catch (err) {
        errorMessage = err.message;
      }
      console.log(responsedata);
      if(!!responsedata && !errorMessage) {
        notification = '+'+JSON.stringify(responsedata);
      } else {
        notification = `Failed to remove member from project (${errorMessage})`;
      }

    }

</script>

<style>
</style>

<ToastNotify {notification}/>

<div>
	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>

		<!-- in the AsyncWrapper tag, the let:dataFromAwaitable should be written as-is -->
		<!-- dataFromAwaitable is a reactive variable containing the return value from yourAsyncFunctionGoesHere() -->
<h1>Project Members</h1>

<DataTable
  headers={[
    { key: "uid", value: "UID" },
    { key: "un", value: "Username" },
    { key: "role", value: "Role"},
    { key: "ad", value: ""}
    ]}
  rows={dataFromAwaitable}
  >
    <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "ad" && !!cell.value}
      <Button
        on:click={clickedRemove(projAD, cell.value)}
        >Remove User</Button>
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>

  </DataTable>
	</AsyncWrapper>

{#if fetchComplete()}
  <br/><br/><br/>
	<h2>Add User To Project</h2>

<p>Search username:</p>
<Search bind:value={s_username}/>
<Button on:click={clickedSearch}>Search</Button>
<br/><br/><br/>

<h3>Results</h3>
<DataTable
  headers={[
    { key: "uid", value: "UID" },
    { key: "un", value: "Username" },
    { key: "role", value: "Role" },
    { key: "us", value: "Data" },
    { key: "ad", value: ""}
  ]}
  rows={[{"uid":d_uid, "un":d_username, "role":d_role, "us":d_data, "ad":d_ad}]}
  >
    <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "ad" && !!cell.value}
      <Button
        on:click={clickedAdd(projAD, cell.value)}
        >Allow Access</Button>
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>
</DataTable>
{/if}

</div>

