<script>

 import { Grid, Row, Column } from "carbon-components-svelte";
  import {
    DataTable,
    Toolbar,
    ToolbarContent,
    ToolbarSearch,
    ToolbarMenu,
    ToolbarMenuItem,
  } from "carbon-components-svelte";
  import { Button, ButtonSet, TextInput } from "carbon-components-svelte";
  import { Link, navigate } from "svelte-navigator";
 import { Link as CCLink } from "carbon-components-svelte";

 import BareMetalServer from "carbon-icons-svelte/lib/BareMetalServer.svelte";
 import RepoSourceCode from "carbon-icons-svelte/lib/RepoSourceCode.svelte";
 import Add from "carbon-icons-svelte/lib/Add.svelte";

 import AsyncWrapper from './AsyncWrapper.svelte';
	import SecSpacer from './SecSpacer.svelte';
	import Comments from './Comments.svelte';
	import IssuesList from './IssuesList.svelte';

		import { FormatDate } from './lib/utils';

    export let usr;
    export let nodeAD;


    let fetchData = async () => {
      let nodesdata = await usr.getChildNodesByType(nodeAD,["proj","page","issue"],["m","s1","n1"]);
      let data = {};
      data.thisproject = {};
      data.subprojects = [];
      data.pages = [];
      data.issues = [];
      if(!!nodesdata && nodesdata.result_nodes) {
        nodesdata.result_nodes.forEach(e => {
          switch(e.ty) {
            case 'proj':
              if(e.ad == nodeAD) {
                data.thisproject = e;
              } else {
                data.subprojects.push(e);
              }
              break;
            case 'page':
              data.pages.push(e);
              break;
            case 'issue':
              data.issues.push(e);
              break;
          }
        });
      }
      return data;
      //return nodesdata.result_nodes;
    }

    let reloadWithNewAD = async (nextAD) => {
      navigate(`/ui/usr/proj-view/${nextAD}`); //only pushes history, doesnt actually reload
      nodeAD = nextAD;
    }


</script>

<style>
</style>
{#key nodeAD}

<div>
	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>

<Grid>

<Row>
<Column>

<h1>{dataFromAwaitable.thisproject.s1 || "[Untitled]"}</h1>
<span>Project ID: {dataFromAwaitable.thisproject.id} &nbsp;&nbsp; | &nbsp;&nbsp; Last Modified: {FormatDate(dataFromAwaitable.thisproject.m)}</span>

</Column>
</Row>

<Row>
<Column>
<SecSpacer hclass="40"/>
</Column>
</Row>


<Row>
<Column>
<ButtonSet>
  <Button on:click={() => navigate("/ui/usr/proj-hosts/"+nodeAD)} icon={BareMetalServer}>Hosts</Button>
  <Button on:click={() => navigate("/ui/usr/item-view/repos/"+nodeAD)} icon={RepoSourceCode}>Repos</Button>
</ButtonSet>
</Column>
</Row>

<Row>
<Column>
<SecSpacer hclass="40"/>
</Column>
</Row>


<Row>
<Column>
<DataTable
  sortable
  title="Sub Projects"
  description="List of child projects under the current project"
  headers={[
    { key: "s1", value: "Title", width: "70%" },
    { key: "m", value: "Last Modified", width: "30%" }
    ]}
  rows={dataFromAwaitable.subprojects}
  >

  <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "s1"}
      <CCLink on:click={() => reloadWithNewAD(row.ad)}>
      {cell.value || "[Untitled]"}
      </CCLink>
    {:else if cell.key === "m"}
      {FormatDate(cell.value)}
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>


<Toolbar size="sm">
    <ToolbarContent>
      <ToolbarSearch
        persistent
        shouldFilterRows
        />
      <Button icon={Add} size="small" kind="tertiary" on:click={() => navigate("/ui/usr/proj-add/"+nodeAD)}>New Sub Project</Button>
    </ToolbarContent>
  </Toolbar>

</DataTable>

</Column>
</Row>

<Row>
<Column>
<SecSpacer hclass="40"/>
</Column>
</Row>


<Row>
<Column>
<DataTable
  sortable
  title="Pages"
  description="List of project pages within the current project"
  headers={[
    { key: "s1", value: "Title", width: "70%" },
    { key: "m", value: "Last Modified", width: "30%" }
    ]}
  rows={dataFromAwaitable.pages}
  >

  <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "s1"}
      <Link
        to={"/ui/usr/item-view/"+row.ty+"/"+row.ad}
        >{cell.value || "[Untitled]"}</Link>
    {:else if cell.key === "m"}
      {FormatDate(cell.value)}
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>


<Toolbar size="sm">
    <ToolbarContent>
      <ToolbarSearch
        persistent
        shouldFilterRows
        />
      <Button icon={Add} size="small" kind="tertiary" on:click={() => navigate("/ui/usr/item-add/page/"+nodeAD)}>New Page</Button>
    </ToolbarContent>
  </Toolbar>

</DataTable>

</Column>
</Row>


<Row>
<Column>
<SecSpacer hclass="40"/>
</Column>
</Row>

<Row>
<Column>

    <IssuesList issues={dataFromAwaitable.issues} {nodeAD} />

</Column>
</Row>

<Row>
<Column>
<SecSpacer hclass="40"/>
</Column>
</Row>

<Row>
<Column>

<Comments {usr} parentAD={nodeAD}/>

</Column>

</Row>
</Grid>

	</AsyncWrapper>

</div>


{/key}

