<script>

  import PortResults from './PortResults.svelte';
	import AsyncWrapper from './AsyncWrapper.svelte';
import Comments from './Comments.svelte';

    export let usr;
    export let portAD;

    let fetchData = async () => {
      let resp = await usr.getPortResults(portAD);
      let portNode;
      let nodesdata = resp.result_nodes;
      let resultsNodes = [];

      nodesdata.forEach(n => {
        switch (n.ty) {
          case "port":
            portNode = n;
            break;
          case "result":
            resultsNodes.push(n);
            break;
        }
      });

      portNode.results = resultsNodes;
      return portNode;
    }
</script>

<style>
</style>

	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>
<PortResults portNode={dataFromAwaitable}/>

	</AsyncWrapper>

<Comments {usr} parentAD={portAD}/>
