<script>
  import { Button, Modal, TextInput, PasswordInput } from "carbon-components-svelte";
  import Login from "carbon-icons-svelte/lib/Login.svelte";

    export let cogged;

    let showLogin = false;
    let showSsoLogin = false;
    let ssoName = "OIDC";
    let ssoUrl = "/sso/";

	cogged.LoggedIn.subscribe((isLoggedIn) => {
		showLogin = !isLoggedIn;
	});

	cogged.ClientConfigLoaded.subscribe((loadedConfig) => {
		showSsoLogin = (!!cogged.ClientConfig && !!cogged.ClientConfig.ssourl);
		ssoName = (!!cogged.ClientConfig && !!cogged.ClientConfig.ssoname) ? cogged.ClientConfig.ssoname : ssoName;
		ssoUrl = (!!cogged.ClientConfig && !!cogged.ClientConfig.ssourl) ? cogged.ClientConfig.ssourl : ssoUrl;
	});

	function closeLoginModal() {
		cogged.LoggedIn.set(true);
		//showLogin = false;
	}

    let userValue;
    let passValue;
    let showPassword = false;

    let clickedLogin = async () => {
        await cogged.DoLogin(userValue, passValue);
    }

    let clickedStdLogin = () => {
        showSsoLogin = false;
    }

    const onVisibilityChange = (visible) => {
        showPassword = visible;
    };
</script>

<!--Modal opened={showLogin} target={'body'} on:close={closeLoginModal} title="Login">

    <TextInput
    placeholder="Your Username"
    label="Username"
    bind:value={userValue}
  />
  <PasswordInput label="Your password" bind:value={passValue} visible={showPassword} {onVisibilityChange} />
  <Button fullSize on:click={clickedLogin}>Login</Button>
</Modal-->


{#if showSsoLogin}
<Modal
  passiveModal
  bind:open={showLogin}
  size="sm"
  preventCloseOnClickOutside
  hasScrollingContent
  modalHeading="Login"
  selectorPrimaryFocus="#btn_login"
  on:open
  on:close={() => closeLoginModal()}
>
  <Button id="btn_login" icon={Login} iconDescription="Login" kind="tertiary" href={ssoUrl}>Login With {ssoName}</Button>
  <Button icon={Login} iconDescription="Login" kind="ghost" on:click={clickedStdLogin}>Standard Login Form</Button>

</Modal>
{:else}
<Modal
  bind:open={showLogin}
  size="sm"
  preventCloseOnClickOutside
  hasScrollingContent
  modalHeading="Login"
  primaryButtonText="Go"
  secondaryButtonText="Cancel"
  selectorPrimaryFocus="#ti_username"
  on:click:button--secondary={() => closeLoginModal()}
  on:open
  on:close={() => closeLoginModal()}
  on:submit={clickedLogin}
>
  <p>Enter your username and password and click "Go".</p>
  <br />
  <TextInput
    id="ti_username"
    labelText="Username"
    placeholder="Enter username..."
    bind:value={userValue}
  />
  <br />
  <PasswordInput
    id="ti_password"
  labelText="Password"
  placeholder="Enter password..."
  bind:value={passValue}
    />

</Modal>
{/if}

