<script>
	import { FormatDate } from './lib/utils';

export let usr;
export let subjectnode;

const getNodeUser = async () => {
        if (!!subjectnode && !!subjectnode.own.uid) {
            let un = await usr.getUsernameForUid(subjectnode.own.uid);
            return un;
        }
        else
            return "";
    };
const getNodeLastModDate = () => {
        if (!!subjectnode)
            return FormatDate(subjectnode.m) || "-"
        else
            return "";
    };

</script>

<style>
.metatext {
    font-size: 0.75em;
    color: grey;
}
</style>

<div>

            <span class="metatext">Owner: {#await getNodeUser()}
		-
	{:then un}
		{un}
	{:catch error}
		unknownuser
	{/await} - Last Modified {getNodeLastModDate()}</span>

</div>


