<script>

  import { Button, TextInput } from "carbon-components-svelte";
import ToastNotify from "./ToastNotify.svelte";

    export let sys;

    let notification = null;

    let username = 'username';
    let d_username = '';
    let d_password = '';
    let d_udata = '';
    let d_role = '';

    let clickedCreate = async () => {
      let userdata = {}
      userdata.username = d_username;
      userdata.password = d_password;
      userdata.us = d_udata;
      userdata.role = d_role;
      let responsedata = null;
      let errorMessage = "";
      try {
        responsedata = await sys.addUser(userdata);
      } catch (err) {
        errorMessage = err.message;
      }
      if(!!responsedata && !!responsedata.created_uids && !!responsedata.created_uids.newuser) {
        notification = "+ created user uid: "+responsedata.created_uids.newuser;
      } else {
        notification = `Failed to create user (${errorMessage})`;
      }

    }

</script>

<style>
</style>

<ToastNotify {notification}/>

	<h1>User Create</h1>

<TextInput bind:value={d_username} labelText="User name" placeholder="Enter user name..." />
<TextInput bind:value={d_password} labelText="Password" placeholder="Enter password..." />
<TextInput bind:value={d_role} labelText="Role" placeholder="Enter role..." />
<TextInput bind:value={d_udata} labelText="User Data (public)" placeholder="Enter user data string..." />
<br/>
<Button on:click={clickedCreate}>Create</Button>

