<script>

 import { DataTable } from "carbon-components-svelte";
  import { Button, TextInput } from "carbon-components-svelte";
	import AsyncWrapper from './AsyncWrapper.svelte';
  import { Link } from "svelte-navigator";
  import PortResults from './PortResults.svelte';
  import IssuesList from './IssuesList.svelte';
  import Comments from './Comments.svelte';

    export let usr;
    export let nodeAD;

    let hostIdString;

    let fetchData = async () => {
      let resp = await usr.getHostPortsAndResults(nodeAD);
      let portsData = [];
      let nodesdata = resp.result_nodes;
      let resultsLookup = {};
      let issuesData = [];
      let hostNode = {}

      nodesdata.forEach(n => {
        switch (n.ty) {
          case "port":
            //if(!hostIdString) hostIdString = n.id.split(":")[0];
            portsData.push(n);
            //lookup[n.id] = e;
            break;
          case "result":
            //if(!tmphost.results) tmphost.results = [];
            //tmphost.ports.push({name: idparts[1], ad: pn.ad});
            resultsLookup[n.ad] = n;
            break;
          case "issue":
            issuesData.push(n);
            break;
          case "host":
            hostNode = n;
            hostIdString = n.s1;
            break;
        }
      });

      portsData.forEach(n => {
        if(!n.results) n.results = [];
        if(n.e)
          n.e.forEach(en => {
            let k = resultsLookup[en.ad];
            if(!!k)
              n.results.push(k);
          });
        console.log(`portsdata foreach ${n.id} ${JSON.stringify(n.results)}`)
      });
      /*
      let portsdata = await usr.getHostsPorts(rootIds);
      if(!!portsdata.result_nodes)
        portsdata.result_nodes.forEach(pn => {
          let idparts = pn.id.split(":");
          let hostid = idparts[0];
          let tmphost = lookup[hostid];
          if(!tmphost.ports) tmphost.ports = [];
          tmphost.ports.push({name: idparts[1], ad: pn.ad});
        });
        */
      return {
        ports: portsData,
        issues: issuesData,
        host: hostNode
      }//portsData;//nodesdata.result_nodes;
    }

    const getHostnames = (hostNode) => {
      if (!!hostNode.s4) {
        let s = hostNode.s4.replace(/,/g, " | ");
        return s;
      }
      return "";
    }

    const sorter = (nA,nB) => {
      let inA = parseInt(nA.id.split(":")[1].split("/")[0], 10);
      let inB = parseInt(nB.id.split(":")[1].split("/")[0], 10);
      let comp = inA - inB;
      if(comp < 0)
          return -1;
      if(comp > 0)
          return 1;
      return 0;
    };

</script>

