<script>

  import { Button, Checkbox, TextInput } from "carbon-components-svelte";
import ToastNotify from "./ToastNotify.svelte";
  import { navigate } from "svelte-navigator";

    export let usr;
    export let parentAD;
    export let type;

    let notification = null;

    let id = 'item_id';
    let title = 'title';

    let permWrite = false;
    let permInEdges = false;
    let permShare = false;

    let clickedCreate = async () => {
      let nodedata = {}
      nodedata.id = id;
      nodedata.ty = type;
      nodedata.s1 = title;
      if(permWrite) nodedata.w = true;
      if(permInEdges) nodedata.i = true;
      if(permShare) nodedata.s = true;

      let responsedata = null;
      let errorMessage = "";
      try {
        responsedata = await usr.addNodeData(parentAD, nodedata);
      } catch (err) {
        errorMessage = err.message;
      }
      console.log(responsedata);
      if(!!responsedata && !!responsedata.created_nodes) {
        //notification = '+ '+JSON.stringify(responsedata);
        let newAD = responsedata.created_nodes["$1"].ad;
        if(newAD)
          navigate(`/ui/usr/item-view/${type}/${newAD}`);
      } //else {
        notification = `Failed to create node (${errorMessage})`;
      //}

    }

</script>

<style>
</style>

<ToastNotify {notification}/>

	<h1>Create Project Item ({type})</h1>

<TextInput bind:value={id} labelText="Item ID" placeholder="Enter Item ID..." />
<br/>
<TextInput bind:value={title} labelText="Item Name/Title" placeholder="Enter Item Title..." />
<br/>
<Checkbox labelText="Other users can modify" bind:checked={permWrite} />
<Checkbox labelText="Other users can link to this item" bind:checked={permInEdges} />
<Checkbox labelText="Other users can share this item" bind:checked={permShare} />

<Button on:click={clickedCreate}>Create</Button>

