<script>

  import { Button, TextInput } from "carbon-components-svelte";
import ToastNotify from "./ToastNotify.svelte";

    export let usr;
    export let parentAD;

    let notification = null;

    let id = 'project_id';
    let title = 'title';

    let clickedCreate = async () => {
      let nodedata = {}
      nodedata.id = id;
      nodedata.s1 = title;
      let responsedata = null;
      let errorMessage = "";
      try {
        responsedata = await usr.addProject(parentAD, nodedata);
      } catch (err) {
        errorMessage = err.message;
      }
      console.log(responsedata);
      if(!!responsedata && !!responsedata.created_nodes) {
        notification = '+ '+JSON.stringify(responsedata);
      } else {
        notification = `Failed to create node (${errorMessage})`;
      }

    }

</script>

<style>
</style>

<ToastNotify {notification}/>

	<h1>Create Sub Project</h1>

<TextInput bind:value={id} labelText="Project ID" placeholder="Enter Project ID..." />
<br/>
<TextInput bind:value={title} labelText="Project Name/Title" placeholder="Enter Project Title..." />
<br/>
<Button on:click={clickedCreate}>Create</Button>

