<script>

 import { DataTable, Toolbar,
    ToolbarContent,
    ToolbarSearch,
    ToolbarMenu,
    ToolbarMenuItem } from "carbon-components-svelte";
  import { Button, TextInput } from "carbon-components-svelte";
	import AsyncWrapper from './AsyncWrapper.svelte';
  import { Link, navigate } from "svelte-navigator";
 import Add from "carbon-icons-svelte/lib/Add.svelte";

    export let usr;
    export let nodeAD;

    let tableData;

    let fetchData = async () => {
      let nodesdata = await usr.getHostNodes(nodeAD);
      tableData = nodesdata.result_nodes;
      if(!!tableData) {
        let lookup = {};
        let rootIds = [];
        tableData.forEach(e => {
          rootIds.push(e.ad);
          e.ports = [];
          lookup[e.id] = e;
        });
        let portsdata = await usr.getHostsPorts(rootIds);
        if(!!portsdata.result_nodes)
          portsdata.result_nodes.forEach(pn => {
            let idparts = pn.id.split(":");
            let hostid = idparts[0];
            let tmphost = lookup[hostid];
            tmphost.ports.push({name: idparts[1], ad: pn.ad});
          });
      } else {
        tableData = [];
      }
      return tableData;//nodesdata.result_nodes;
    }

    const hostnameList = (commaSepList) => {
      if(!commaSepList)
        return [];
      return commaSepList.split(",");
    }



</script>

<style>
</style>


<div>

	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>
{#if dataFromAwaitable.length > 0}


<DataTable
  sortable
  title="Hosts"
  description="List of hosts within the current project"
  headers={[
    { key: "addr", value: "Addresses" },
    { key: "s4", value: "Hostnames" },
    { key: "ports", value: "View" }
    ]}
  rows={dataFromAwaitable}
  >

  <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "addr"}
      <ul>
      {#if row.s1}
         <li><Link to={"/ui/usr/item-view/host/"+row.ad}>{row.s1}</Link></li>
      {/if}
      {#if row.s2}
         <li><Link to={"/ui/usr/item-view/host/"+row.ad}>{row.s2}</Link></li>
      {/if}
      {#if row.s3}
         <li><Link to={"/ui/usr/item-view/host/"+row.ad}>{row.s3}</Link></li>
      {/if}
      </ul>
    {:else if cell.key === "ports"}
      {#each row.ports as port}
         <Link to={"/ui/usr/item-view/port/"+port.ad}>{port.name}</Link>&nbsp;|&nbsp;
      {/each}
    {:else if cell.key === "s4"}
      <ul>
      {#each hostnameList(cell.value) as hostname}
         <li>{hostname}</li>
      {/each}
      </ul>
    {:else if !cell.value}
      -
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>


<Toolbar size="sm">
    <ToolbarContent>
      <ToolbarSearch
        persistent
        shouldFilterRows
        />
      <Button icon={Add} size="small" kind="tertiary" on:click={() => navigate("/ui/usr/item-add/host/"+nodeAD)}>New Host</Button>
    </ToolbarContent>
  </Toolbar>

</DataTable>

{:else}
<h4>No hosts found</h4>
{/if}
</AsyncWrapper>


</div>
