<script>

 import { Search } from "carbon-components-svelte";
  import { Button, TextInput, DataTable } from "carbon-components-svelte";

    export let sys;

    let username = 'username';
    let d_username = '';
    let d_uid = '';
    let d_role = '';
    let d_data = '';

    let clickedSearch = async () => {
      try {
        let userdata = await sys.getUserInfo(username);
        d_username = userdata.user.un;
        d_uid = userdata.user.uid;
        d_role = userdata.user.role;
        d_data = userdata.user.us;
      } catch (err) {
        d_username = err.message;
        d_uid = '';
        d_role = '';
        d_data = '';
      }
    }

</script>

<style>
</style>


	<h1>User Query</h1>

<Search bind:value={username}/>
<Button on:click={clickedSearch}>Search</Button>
<br/><br/><br/>

<h3>Results</h3>
<DataTable
  headers={[
    { key: "uid", value: "UID" },
    { key: "un", value: "Username" },
    { key: "role", value: "Role" },
    { key: "us", value: "Data" },
  ]}
  rows={[{"uid":d_uid, "un":d_username, "role":d_role, "us":d_data}]}
  />


