<script>
  import {
    Header,
    HeaderUtilities,
    HeaderGlobalAction,
    SideNav,
    SideNavItems,
    SideNavMenu,
    SideNavMenuItem,
    SideNavLink,
    SkipToContent,
    Content,
    Grid,
    Row,
    Column,
  } from "carbon-components-svelte";
  import Logout from "carbon-icons-svelte/lib/Logout.svelte";
  import SettingsAdjust from "carbon-icons-svelte/lib/SettingsAdjust.svelte";
  import UserAvatarFilledAlt from "carbon-icons-svelte/lib/UserAvatarFilledAlt.svelte";

  import { Router, Link, Route } from "svelte-navigator";

  import { CoggedApi } from './lib/cogged';
  import { Sys } from './lib/sys';
  import { Adm } from './lib/adm';
  import { Usr } from './lib/usr';

  import Login from './Login.svelte';
  import Home from './Home.svelte';
  import Content1 from './Content1.svelte';
  import Content2 from './Content2.svelte';
  import SysUserQuery from './SysUserQuery.svelte';
  import SysUserAdd from './SysUserAdd.svelte';
  import SysOrgList from './SysOrgList.svelte';
  import SysOrgAdd from './SysOrgAdd.svelte';
  import SysOrgAdmins from './SysOrgAdmins.svelte';
  import AdmOrgList from './AdmOrgList.svelte';
  import AdmTeamAdd from './AdmTeamAdd.svelte';
  import AdmTeamList from './AdmTeamList.svelte';
  import AdmProjList from './AdmProjList.svelte';
  import AdmProjAdd from './AdmProjAdd.svelte';
  import AdmProjEdit from './AdmProjEdit.svelte';
  import UsrProjList from './UsrProjList.svelte';
  import UsrProjDashboard from './UsrProjDashboard.svelte';
  import UsrProj from './UsrProj.svelte';
  import UsrProjAdd from './UsrProjAdd.svelte';
  import ItemAdd from './ItemAdd.svelte';
  import UsrPage from './UsrPage.svelte';
  import UsrIssue from './UsrIssue.svelte';
  import UsrHosts from './UsrHosts.svelte';
  import UsrHost from './UsrHost.svelte';
  import UsrPort from './UsrPort.svelte';
  import UsrResult from './UsrResult.svelte';

  import "carbon-components-svelte/css/all.css";


    let isSideNavOpen = false;

	let cogged = new CoggedApi('/api');
	cogged.LoadClientConfig();
	cogged.CheckLogin();

	let currentUid;
    cogged.currentUserId.subscribe((value) => {
        currentUid = value;
    });

    let currentRole;
    cogged.currentUserRole.subscribe((value) => {
        currentRole = value;
    });

      let sys = new Sys(cogged);
      let adm = new Adm(cogged);
      let usr = new Usr(cogged);

</script>

<svelte:head>
  <link href="https://cdnjs.cloudflare.com/ajax/libs/quill/2.0.2/quill.snow.min.css" rel="stylesheet" />
</svelte:head>


<Login {cogged}></Login>

<Router>

<Header company="Secquiry" platformName="ASPM" bind:isSideNavOpen>
  <svelte:fragment slot="skip-to-content">
    <SkipToContent />
  </svelte:fragment>
  <HeaderUtilities>
    <HeaderGlobalAction
      iconDescription="Settings"
      tooltipAlignment="start"
      icon={SettingsAdjust}
    />
    <HeaderGlobalAction iconDescription="Profile" icon={UserAvatarFilledAlt} />
    <HeaderGlobalAction
      iconDescription="Log out"
      tooltipAlignment="end"
      icon={Logout}
      on:click={() => cogged.DoLogout()}
    />
  </HeaderUtilities>
</Header>

<SideNav bind:isOpen={isSideNavOpen}>
  <SideNavItems>
{#if currentRole == "sys"}
    <Link to="/ui/sys/user-query">
      <SideNavLink text="Find User" />
    </Link>
    <Link to="/ui/sys/user-add">
      <SideNavLink text="Add User" />
    </Link>
    <Link to="/ui/sys/org-list">
      <SideNavLink text="Organisations" />
    </Link>
    <Link to="/ui/sys/org-add">
      <SideNavLink text="Add Organisation" />
    </Link>
{/if}
{#if currentRole == "adm"}
    <Link to="/ui/adm/org-list">
      <SideNavLink text="Organisations" />
    </Link>
{/if}
{#if currentRole == "user"}
    <Link to="/ui/usr/proj-list">
      <SideNavLink text="My Projects" />
    </Link>
{/if}

    <Link to="/ui/accounts/1001">
      <SideNavLink text="Link 1" />
    </Link>
    <Link to="/ui/c1">
      <SideNavLink text="Link 2" />
    </Link>
    <SideNavLink text="Link 3" />
    <SideNavMenu text="Menu">
      <SideNavMenuItem href="/" text="Link 1" />
      <SideNavMenuItem href="/" text="Link 2" />
      <SideNavMenuItem href="/" text="Link 3" />
    </SideNavMenu>
  </SideNavItems>
</SideNav>

<Content>

  <Route path="/">
      <Home/>
  </Route>
  <Route path="/ui/sys/user-query">
      <SysUserQuery {sys}/>
  </Route>
  <Route path="/ui/sys/user-add">
      <SysUserAdd {sys}/>
  </Route>
  <Route path="/ui/sys/org-list">
      <SysOrgList {sys}/>
  </Route>
  <Route path="/ui/sys/org-add">
      <SysOrgAdd {sys}/>
  </Route>
  <Route path="/ui/sys/org-admins/:id" let:params>
      <SysOrgAdmins {sys} orgAD={params.id}/>
  </Route>
  <Route path="/ui/adm/org-list">
      <AdmOrgList {sys}/>
  </Route>
  <Route path="/ui/adm/org-teams/:id" let:params>
      <AdmTeamList {adm} orgAD={params.id}/>
  </Route>
  <Route path="/ui/adm/team-add/:id" let:params>
      <AdmTeamAdd {adm} orgAD={params.id}/>
  </Route>
  <Route path="/ui/adm/team-projs/:id" let:params>
      <AdmProjList {adm} teamAD={params.id}/>
  </Route>
  <Route path="/ui/adm/proj-add/:id" let:params>
      <AdmProjAdd {adm} teamAD={params.id}/>
  </Route>
  <Route path="/ui/adm/proj/:id" let:params>
      <AdmProjEdit {adm} {sys} projAD={params.id}/>
  </Route>
  <Route path="/ui/usr/proj-list">
      <UsrProjList {usr} />
  </Route>
  <Route path="/ui/usr/proj-view/:id" let:params>
      <UsrProj {usr} nodeAD={params.id}/>
  </Route>
  <Route path="/ui/usr/proj-dashboard/:id" let:params>
      <UsrProjDashboard {usr} projAD={params.id}/>
  </Route>
  <Route path="/ui/usr/proj-hosts/:id" let:params>
      <UsrHosts {usr} nodeAD={params.id}/>
  </Route>
  <Route path="/ui/usr/proj-add/:id" let:params>
      <UsrProjAdd {usr} parentAD={params.id}/>
  </Route>
  <Route path="/ui/usr/item-add/:type/:id" let:params>
      <ItemAdd {usr} type={params.type} parentAD={params.id}/>
  </Route>
  <Route path="/ui/usr/item-view/page/:id" let:params>
      <UsrPage {usr} nodeAD={params.id}/>
  </Route>
  <Route path="/ui/usr/item-view/issue/:id" let:params>
      <UsrIssue {usr} nodeAD={params.id}/>
  </Route>
  <Route path="/ui/usr/item-view/host/:id" let:params>
      <UsrHost {usr} nodeAD={params.id}/>
  </Route>
  <Route path="/ui/usr/item-view/port/:id" let:params>
      <UsrPort {usr} portAD={params.id}/>
  </Route>
  <Route path="/ui/usr/item-view/result/:id" let:params>
      <UsrResult {usr} resultAD={params.id}/>
  </Route>
  <Route path="/ui/c1">
      <Content1/>
  </Route>
  <Route path="/ui/accounts/:id" let:params>
      <Content2 exampleParam={params.id}/>
  </Route>

</Content>

</Router>
