<script>

  import { Button, TextInput } from "carbon-components-svelte";
	import AsyncWrapper from './AsyncWrapper.svelte';
  import { Link, navigate } from "svelte-navigator";
 import { DataTable } from "carbon-components-svelte";


import { onMount } from 'svelte';
import Quill from "quill";
import ToastNotify from "./ToastNotify.svelte";
import Comments from './Comments.svelte';
import NodeMetadata from './NodeMetadata.svelte';

	import { QuillToolbarOptions } from './lib/utils';

import { Packer } from "docx";
import { htmlToDocx } from "./lib/html2docx";

    let notification = null;

    export let usr;
    export let nodeAD;

let quill = null;
let quillEditElement;
let node;
let title;
let canEditPage = false;

//let lastmodts;
//let owneruid;

const createQuill = (element) => {
  return new Quill(element, {
    theme: "snow",
    modules: {
      toolbar: QuillToolbarOptions //true, //document.getElementById('toolbarId')
    },
      placeholder: 'Compose an epic...',
  });
};


onMount(() => {
    quill = createQuill(quillEditElement);
});


const logContent = () => {
	let c = quill.getContents();
	console.log(JSON.stringify(c));
}

const saveContent = async () => {
	let c = quill.getContents();
	let serialisedData = JSON.stringify(c.ops);
	node.s4 = serialisedData;
    node.s1 = title;

    let responsedata = null;
    let errorMessage = "";
    try {
      responsedata = await usr.updateNodeData(node);
    } catch (err) {
      errorMessage = err.message;
    }
    console.log(responsedata);
    if(!!responsedata && !!responsedata.timestamp) {
      notification = '+'+JSON.stringify(responsedata);
    } else {
      notification = `Failed to save note (${errorMessage})`;
    }

}

const setContent = () => {
	quill.setContents([
	{ insert: 'Hello ' },
	{ insert: 'World!', attributes: { bold: true } },
	{ insert: '\n' },
	]);
}

let testdelta = '{"ops":[{"insert":"Hello "},{"attributes":{"bold":true},"insert":"World!"},{"insert":"\\ndum dum\\n\\nalej fklsj"},{"attributes":{"list":"ordered"},"insert":"\\n"},{"insert":"v"},{"attributes":{"list":"ordered"},"insert":"\\n"},{"insert":"avj skevjlksjv"},{"attributes":{"list":"ordered"},"insert":"\\n\\n"},{"insert":"va"},{"attributes":{"list":"ordered"},"insert":"\\n"},{"insert":"sjvkjslvj lasv\\n\\nsavjasvlsjaklv j"},{"attributes":{"header":2},"insert":"\\n"},{"insert":"sv"},{"attributes":{"header":2},"insert":"\\n"},{"insert":"as"},{"attributes":{"header":2},"insert":"\\n"},{"insert":"ev\\nasvhjao "},{"attributes":{"link":"javascript:alert(123)"},"insert":"s8ivh38hf0293yu"},{"insert":"\\nrfashjgfahw83hf uahwef\\n"}]}';

const setContent2 = () => {
	quill.setContents(JSON.parse(testdelta).ops);
}




    let fetchData = async () => {
      let nodesdata = await usr.getPageData(nodeAD);
      let nodes = nodesdata.result_nodes;
      node = nodes[0];
      let deserialisedData = [];
      try {
        deserialisedData = JSON.parse(node.s4);
      } catch (err) {
        console.log(err);
      }
      title = node.s1;
      canEditPage = usr.canWrite(node);
      quill.setContents(deserialisedData);
      if(canEditPage)
        quill.enable();
      else
        quill.disable();
      return nodes;
    }

    let fetchPageChildNodes = async () => {
      console.log(`fetchPageChildNodes nodeAD:${nodeAD}`);
      let nodesdata = await usr.getAllChildNodes(nodeAD, ["s1"]);
      let returnData = {pages: [], comments: [], files: []};
      nodesdata.result_nodes.forEach(n => {
        switch(n.ty) {
          case "page":
            console.log(`n.ad:${n.ad} nodeAD:${nodeAD}`);
            if(n.ad != nodeAD)
              returnData.pages.push(n);
            break;
          case "comment":
            returnData.comments.push(n);
            break;
          case "file":
            returnData.files.push(n);
            break;
        }
      });
      return returnData;
    }

const reloadWithNewAD = async (nextAD) => {
    navigate(`/ui/usr/item-view/page/${nextAD}`); //only pushes history, doesnt actually reload
    nodeAD = nextAD;
}

function saveBlob(blob, fileName) {
    var a = document.createElement("a");
    document.body.appendChild(a);
    a.style = "display: none";

    var url = window.URL.createObjectURL(blob);
    a.href = url;
    a.download = fileName;
    a.click();
    window.URL.revokeObjectURL(url);
};

const saveAsDocx = async () => {
  let html = quill.getSemanticHTML();
  let doc = await htmlToDocx(html);
  Packer.toBlob(doc).then((blob) => {
      saveBlob(blob, 'export.docx');
  });
};

</script>

<style>
</style>


<ToastNotify {notification}/>

<div>
{#key nodeAD}
	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>

	</AsyncWrapper>
{/key}

{#key node}
	<NodeMetadata {usr} subjectnode={node} />
	<br/><br/>
	<TextInput readonly='{!canEditPage}' bind:value={title} labelText="Page Name/Title" placeholder="Enter Page Title..." />
{/key}

<br/>
{#if canEditPage}<button on:click={saveContent}>Save</button>{/if} <!--button on:click={logContent}>log</button> <button on:click={setContent}>reset</button> <button on:click={setContent2}>reset2</button-->
<button on:click={saveAsDocx}>Export Docx</button>
<div bind:this={quillEditElement}></div>

{#key nodeAD}

	<AsyncWrapper awaitable={() => fetchPageChildNodes()} let:dataFromAwaitable>

<br/><br/>
<h3>Child Pages</h3>
<DataTable
  headers={[
    { key: "ty", value: "Type" },
    { key: "s1", value: "Page" },
    { key: "id", value: "Page ID" },
    { key: "ad", value: "View" }
    ]}
  rows={dataFromAwaitable.pages}
  >

  <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "ad"}
      <button on:click={() => reloadWithNewAD(cell.value)}>View Page</button>

    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>
</DataTable>

	</AsyncWrapper>
{/key}

    <Link to={"/ui/usr/item-add/page/"+nodeAD}>
      <Button>Add New Sub-Page</Button>
    </Link>

</div>
<br/><br/>
<Comments {usr} parentAD={nodeAD}/>
