class Sys {
    constructor(cogged) {
        this._c = cogged;
    }

    addDatatableIdsToArray(arr) {
        if(!!arr && arr.length > 0)
            for(let i=0; i < arr.length; i++) {arr[i].id = i}
        return arr;
    }

    async getThing(id) {
        let data = await this._c.healthStatusGet();
        return `(${id}) health status: ${data.status}`;
    }

    async getUserInfo(username) {
        let data = await this._c.userNameGet(username);
        return data;
    }

    async addUser(user) {
        let data = await this._c.adminUserPut(user);
        return data;
    }

    async getOrganisations(role="sys") {
        let querydata = {"depth":1, "select":["id","c"], "filters":{"and":[{"field":"m","op":"gt","val":"0"},{"field":"ty","op":"eq","val":"org"}]}};
        let data = {};
        if(role == "sys") {
            data = await this._c.userNodesOwnPost(querydata);
        } else if(role == "adm") {
            data = await this._c.userNodesSharedPost(querydata);
        }
        return data;
    }

    async addOrganisation(orgdata) {
        let requestdata = {"node":{"uid":"$1","id":orgdata.id,"ty":"org","r":true,"o":true,"e":[]}};
        let data = await this._c.userNodePut(requestdata);
        return data;
    }

    async getOrgAdmins(orgAD) {
        let data = await this._c.graphSharedwithGet(orgAD);
        return data;
    }

    async addOrgAdmin(orgAD,userAD) {
        let reqdata = {"nodes":[orgAD],"users":[userAD]};
        let data = await this._c.userSharePut(reqdata);
        return data;
    }

    async removeOrgAdmin(orgAD,userAD) {
        let reqdata = {"nodes":[orgAD],"users":[userAD]};
        let data = await this._c.userSharePatch(reqdata);
        return data;
    }

}


export {
    Sys
}
