<script>

  import { Button, TextInput, Dropdown } from "carbon-components-svelte";
	import AsyncWrapper from './AsyncWrapper.svelte';
  import { Link } from "svelte-navigator";
  import Comments from './Comments.svelte';

import { onMount } from 'svelte';
import Quill from "quill";
import ToastNotify from "./ToastNotify.svelte";
import NodeMetadata from './NodeMetadata.svelte';
	import { QuillToolbarOptions } from './lib/utils';
    import { Packer } from "docx";
	import { htmlToDocx } from "./lib/html2docx";

    let notification = null;

    export let usr;
    export let nodeAD;

let quill = null;
let quillEditElement;
let node;
let title;
let selectedSeverity = 0;
//let nodeSeverity = NaN;
let selectedId;
let canEditIssue = false;

onMount(() => {
  //let container = quillEditElement;
  console.log(quillEditElement);
  quill = new Quill(quillEditElement, {
    theme: "snow",
    modules: {
      toolbar: QuillToolbarOptions, //document.getElementById('toolbarId')
    },
      placeholder: 'Compose an epic...',
  });
});


const saveContent = async () => {
	let c = quill.getContents();
	let serialisedData = JSON.stringify(c.ops);
	node.s4 = serialisedData;
    node.s1 = title;
    //node.n1 = selectedSeverity;
    node.n1 = parseInt(selectedId,10);
  console.log("saveContent",JSON.stringify(node))

    let responsedata = null;
    let errorMessage = "";
    try {
      responsedata = await usr.updateNodeData(node);
    } catch (err) {
      errorMessage = err.message;
    }
    console.log(responsedata);
    if(!!responsedata && !!responsedata.timestamp) {
      notification = '+'+JSON.stringify(responsedata);
    } else {
      notification = `Failed to save issue (${errorMessage})`;
    }

}


    let fetchData = async () => {
      let nodesdata = await usr.getIssueData(nodeAD);
      let nodes = nodesdata.result_nodes;
      node = nodes[0];
      setSeverity(node);
      let deserialisedData = [];
      try {
        deserialisedData = JSON.parse(node.s4);
      } catch (err) {
        console.log(err);
      }
      title = node.s1;
      canEditIssue = usr.canWrite(node);
      quill.setContents(deserialisedData);
      if(canEditIssue)
        quill.enable();
      else
        quill.disable();

      quill.setContents(deserialisedData);
      return nodes;
    }

  const setSeverity = (node) => {
    if(!node || !node.n1) {
      selectedId = 0;
    } else {
      //~~ efficiently truncates the decimal part of the variable, turning it into an integer.
      let val = ~~node.n1;
      if(val < 4 || val > 10) {
        selectedId = 0;
      } else {
        selectedId = val;
      }
    }
  console.log("setSeverity",selectedId)
  };


  const onSelected = (e) => {
    selectedSeverity = e.selectedId;
    console.log("onSelected",selectedId,JSON.stringify(e),selectedSeverity);
  };


function saveBlob(blob, fileName) {
    var a = document.createElement("a");
    document.body.appendChild(a);
    a.style = "display: none";

    var url = window.URL.createObjectURL(blob);
    a.href = url;
    a.download = fileName;
    a.click();
    window.URL.revokeObjectURL(url);
};

const saveAsDocx = async () => {
  let html = quill.getSemanticHTML();
  let doc = await htmlToDocx(html);
  Packer.toBlob(doc).then((blob) => {
      saveBlob(blob, 'export.docx');
  });
};
</script>

<style>
</style>


<ToastNotify {notification}/>

<div>
	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>

	</AsyncWrapper>

{#key node}
<NodeMetadata {usr} subjectnode={node} />
{/key}
<br/><br/>

	<TextInput readonly='{!canEditIssue}' bind:value={title} labelText="Issue Name/Title" placeholder="Enter Issue Title..." />
<br/>

<!--  selectedId={nodeSeverity}
{#key nodeSeverity}
-->
<Dropdown
  titleText="Severity"
  bind:selectedId
  disabled='{!canEditIssue}'
  on:select={(e) => onSelected(e) }
  items={[
    { id: 0, text: "New" },
    { id: 10, text: "Critical" },
    { id: 9, text: "High" },
    { id: 8, text: "Medium" },
    { id: 7, text: "Low" },
    { id: 6, text: "Information" },
    { id: 5, text: "Investigating" },
    { id: 4, text: "Closed" },
  ]}
/>


<br/>

{#if canEditIssue}<button on:click={saveContent}>Save</button>{/if}
<button on:click={saveAsDocx}>Export Docx</button>
<div bind:this={quillEditElement}></div>


</div>
<br/><br/>

<Comments {usr} parentAD={nodeAD}/>
