function FormatDate(iso8601date) {
    if(!iso8601date)
        return "-";
    try {
        //return iso8601date.split('.')[0].replace('T',' ')
        const date = new Date(iso8601date);
        let retD = date.toLocaleDateString("en-GB", {year:'numeric',month:'numeric',day:'numeric',hour:'numeric',minute:'numeric',hour12:true});
        return retD.toUpperCase()
    } catch (err) {
        return iso8601date;
    }
}



