<script>

  import PortResults from './PortResults.svelte';
	import AsyncWrapper from './AsyncWrapper.svelte';
import JsonDisplay from './JsonDisplay.svelte';
import SecSpacer from './SecSpacer.svelte';
import Comments from './Comments.svelte';

    export let usr;
    export let resultAD;

    let fetchData = async () => {
      let resp = await usr.getNodeData(resultAD, ["s1","s2","s4","n1"]);
      let n = resp.result_nodes[0];
      try {
        n.detailjson = JSON.parse(n.s4);
        } catch (err) {
          console.log(err);
        }

      return n;
    }

    const severity = (n) => {
        if(!n) return "information";
        if(n > 7.5) return "high";
        if(n > 5.0) return "medium";
        if(n > 2.5) return "low";
        return "information";
    };

</script>

<style>
code {
            white-space: pre-wrap;       /* CSS3 */
            white-space: -moz-pre-wrap;  /* Mozilla */
            white-space: -pre-wrap;      /* Opera 4-6 */
            white-space: -o-pre-wrap;    /* Opera 7 */
            word-wrap: break-word;       /* IE */
            overflow-wrap: break-word;   /* Modern Browsers */
            word-break: break-all;
        }
</style>

	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>

<h2>{dataFromAwaitable.s1}</h2>
<SecSpacer/>

<h3>Location</h3>
<div>{dataFromAwaitable.s2}</div>
<SecSpacer/>

<h3>Severity</h3>
<div>{severity(dataFromAwaitable.n1)}</div>
<SecSpacer/>

<h3>Detail</h3>
<div>
{#if !!dataFromAwaitable.detailjson}
<JsonDisplay obj={dataFromAwaitable.detailjson}/>
{:else}
<pre><code>{dataFromAwaitable.s4}</code></pre>
{/if}
</div>

	</AsyncWrapper>

<Comments {usr} parentAD={resultAD}/>
