<script>
  import { Button, TextInput } from "carbon-components-svelte";
  import { Grid, Row, Column } from "carbon-components-svelte";
  import { Link, navigate } from "svelte-navigator";

 import Add from "carbon-icons-svelte/lib/Add.svelte";
 import Edit from "carbon-icons-svelte/lib/Edit.svelte";
 import EditOff from "carbon-icons-svelte/lib/EditOff.svelte";
 import CheckmarkOutline from "carbon-icons-svelte/lib/CheckmarkOutline.svelte";

 import AsyncWrapper from './AsyncWrapper.svelte';
	import SecSpacer from './SecSpacer.svelte';

	import { FormatDate } from './lib/utils';

export let usr;
export let parentAD;

let newCommentText;
let editCommentText;
let editingComment;
let notificationAdd;
let notificationSave;

let commentList = [];

let clickedEdit = async (node) => {
    editingComment = node;
    notificationSave = null;
}

let clickedCancelEdit = async () => {
    editingComment = null;
}

let clickedSave = async () => {
    let responsedata = null;
    let errorMessage = "";
    try {
        responsedata = await usr.updateNodeData(editingComment);
    } catch (err) {
    errorMessage = err.message;
    }
    console.log(responsedata);
    if(!responsedata) {
        notificationSave = `Failed to save comment (${errorMessage})`;
    } else {
        notificationSave = null;
        editingComment = null;
    }

}

let clickedCreate = async () => {
    let commentdata = {}
    commentdata.id = usr.getMyUserUid()+"/"+(Math.random() + 1).toString(36).substring(4);
    commentdata.ty = "comment";
    commentdata.s4 = newCommentText;
    commentdata.r = true;
    let responsedata = null;
    let errorMessage = "";
    try {
        responsedata = await usr.addNode(parentAD, commentdata);
    } catch (err) {
    errorMessage = err.message;
    }
    console.log(responsedata);
    if(!responsedata || !responsedata.created_nodes) {
        notificationAdd = `Failed to create comment (${errorMessage})`;
    } else {
        notificationAdd = null;
        let newNode = responsedata.created_nodes["$1"]
        newNode.editable = true;
        newNode.ty = commentdata.ty;
        newNode.id = commentdata.id;
        newNode.s4 = commentdata.s4;
        commentList.push(newNode);
        commentListRefresh = {};
        newCommentText = "";
        //commentData.comments.push(newNode);
    }

}

let commentListRefresh;

let fetchComments = async () => {
    //commentList.length = 0;
    let responsedata = await usr.getChildNodesByType(parentAD, ["comment"], ["s4","c"]);
    if (!!responsedata.result_nodes) {
        responsedata.result_nodes.forEach(n => {
            console.log(`my: ${usr.getMyUserUid()}, n.uid: ${n.own.uid}`)
            n.editable = usr.amOwnerOf(n);
            commentList.push(n);
            //commentData.comments.push(n);
        });
        //return responsedata.result_nodes;
        commentListRefresh = {};
    }
    //return [];
}

const getCommentText = (text) => { return text || "[deleted]" };
const getCommentDate = (datestr) => { return FormatDate(datestr) || "-" };

const getUsernameFor = async (uid) => { return await usr.getUsernameForUid(uid) };

//{#key commentListRefresh}
</script>

<style>
.errortext {
    color: red;
}

.commentdate {
    font-size: 0.75em;
    color: grey;
}
</style>

<div>

	<h4>Comments</h4>
    <SecSpacer/>

	<AsyncWrapper awaitable={() => fetchComments()} let:dataFromAwaitable>
    </AsyncWrapper>


{#key commentListRefresh}

{#each commentList as comment}
    {#if !!editingComment && editingComment.uid == comment.uid}
    <TextInput bind:value={editingComment.s4} labelText="Edit comment"/>
    <Button on:click={clickedSave} icon={CheckmarkOutline}/>
    <Button on:click={clickedCancelEdit} icon={EditOff}/>
        {#if !!notificationSave}
            <span class="errortext">{notificationSave}</span>
        {/if}
    {:else}
    <Grid>
    <Row>
    <Column sm={1} md={1} lg={1}>
    {#await getUsernameFor(comment.own.uid)}
		?
	{:then un}
		{un}
	{:catch error}
		?
	{/await}
    </Column>
    <Column sm={2} md={4} lg={8}>
    <span>{getCommentText(comment.s4)}</span> <br/>
    <span class="commentdate">{getCommentDate(comment.c)}</span>
    </Column>
    <Column sm={1} md={1} lg={1}>
        {#if comment.editable}
        <Button kind="ghost" iconDescription="Edit" on:click={() => clickedEdit(comment)} icon={Edit} />
        {/if}
    </Column>
    </Row></Grid>
    {/if}
{/each}

{/key}

{#if !!notificationAdd}
<span class="errortext">{notificationAdd}</span>
{/if}
<TextInput bind:value={newCommentText} labelText="Add a comment" placeholder="your comment..." />
<Button on:click={clickedCreate} icon={Add}/>

</div>


