<script>
	import AsyncWrapper from './AsyncWrapper.svelte';

	let foo = async () => {
		let r = await fetch("https://webhook-test.com/cc78061356328c2d52738bce9e0433ad");
		let rt = await r.text();
		return rt;
	};
</script>

<style>
</style>

<div>
	<AsyncWrapper awaitable={() => foo()} let:dataFromAwaitable>

		<!-- in the AsyncWrapper tag, the let:dataFromAwaitable should be written as-is -->
		<!-- dataFromAwaitable is a reactive variable containing the return value from yourAsyncFunctionGoesHere() -->
		<b>The return value is: <i>{`${dataFromAwaitable}`}</i></b>

	</AsyncWrapper>
</div>
