class Adm {
    constructor(cogged) {
        this._c = cogged;
    }


    async getTeams(orgAD) {
        let querydata = {"depth":1, "root_ids":[orgAD], "select":["id","c"], "filters":{"and":[{"field":"m","op":"gt","val":"0"},{"field":"ty","op":"eq","val":"team"}]}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async addTeam(orgAD, teamdata) {
        let requestdata = {"nodes":[{"uid":"$1","id":teamdata.id,"ty":"team","s1":teamdata.s1,"r":true,"o":true,"e":[]}]};
        let data = await this._c.graphNodesPut(orgAD, requestdata);
        return data;
    }

    async getProjects(teamAD) {
        let querydata = {"depth":1, "root_ids":[teamAD], "select":["id","c","s1"], "filters":{"and":[{"field":"m","op":"gt","val":"0"},{"field":"ty","op":"eq","val":"proj"}]}};
        let data = {};
        data = await this._c.graphNodesPost(querydata);
        return data;
    }

    async addProject(teamAD, projdata) {
        let requestdata = {"reset_sgi":true, "nodes":[
            {"uid":"$1","id":projdata.id,"ty":"proj","r":true,"o":true,"s1":projdata.s1,"e":[]}
            //{"uid":"$1","id":projdata.id,"ty":"proj","r":true,"o":true,"e":[{"uid":"$2"},{"uid":"$3"}]},
            //{"uid":"$2","id":projdata.id+"/hosts","ty":"hosts","r":true,"o":true,"e":[{"uid":"$1"}]},
            //{"uid":"$3","id":projdata.id+"/repos","ty":"repos","r":true,"o":true,"e":[{"uid":"$1"}]}
        ]};
        let data = await this._c.graphNodesPut(teamAD, requestdata);
        return data;
    }

    async getProjMembers(projAD) {
        let data = await this._c.graphSharedwithGet(projAD);
        return data;
    }

    async addProjectMember(projAD,userAD) {
        let reqdata = {"nodes":[projAD],"users":[userAD]};
        let data = await this._c.userSharePut(reqdata);
        return data;
    }

    async removeProjectMember(projAD,userAD) {
        let reqdata = {"nodes":[projAD],"users":[userAD]};
        let data = await this._c.userSharePatch(reqdata);
        return data;
    }

}


export {
    Adm
}
