<script>

 import { DataTable, TreeView, ClickableTile, Grid, Row, Column } from "carbon-components-svelte";
  import { Button, TextInput } from "carbon-components-svelte";
  import { Link, navigate } from "svelte-navigator";

  import AsyncWrapper from './AsyncWrapper.svelte';
	import SecSpacer from './SecSpacer.svelte';
  import IssuesList from './IssuesList.svelte';

    export let usr;
    export let projAD;

    let expandedIds = [];
let issues = [];

    const populateChildProjects = (lookupByUid, projNode) => {
      let childProjects = [];
      let projEdges = projNode.e || [];
      projEdges.forEach(e => {
        let eActual = lookupByUid[e.uid];
        if(eActual && eActual.ty == "proj") {
          childProjects.push(eActual);
          //childProjects.push({id: eActual.ad, text: eActual.s1});
        }
      });
      projNode.children = childProjects;
      projNode.id = projNode.ad;
      expandedIds.push(projNode.id);
      projNode.text = projNode.s1 || "[Untitled]";
      projNode.children.forEach(e => populateChildProjects(lookupByUid,e));
    };

    let fetchData = async () => {
      let nodesdata = await usr.getAllChildProjectsAndIssues(projAD);
      let data = {};
      let lookupProjByUid = {};
      data.thisproject = {};
      data.issues = [];
      data.issueCategories = {
        critical: 0, //10
        high: 0, //9
        medium: 0, //8
        low: 0, //7
        information: 0, //6
        investigating: 0, //5
        closed: 0, //4
        new: 0, //0 or undefined
      };
      if(!!nodesdata && nodesdata.result_nodes) {
        nodesdata.result_nodes.forEach(e => {
          switch(e.ty) {
            case 'proj':
              if(e.ad == projAD) {
                data.thisproject = e;
              } else {
                lookupProjByUid[e.uid] = e;
              }
              break;
            case 'issue':
              e.id = e.ad;
              data.issues.push(e);
              let sev = parseInt(e.n1,10);
              switch (sev) {
                case 10:
                  data.issueCategories.critical++;
                  break;
                case 9:
                  data.issueCategories.high++;
                  break;
                case 8:
                  data.issueCategories.medium++;
                  break;
                case 7:
                  data.issueCategories.low++;
                  break;
                case 6:
                  data.issueCategories.information++;
                  break;
                case 5:
                  data.issueCategories.investigating++;
                  break;
                case 4:
                  data.issueCategories.closed++;
                  break;
                default:
                  data.issueCategories.new++;
                  break;
              }
              break;
          }
        });
        //console.log(JSON.stringify(lookupProjByUid));
        populateChildProjects(lookupProjByUid, data.thisproject);
      }
      issues = data.issues;
      //console.log(JSON.stringify(data));
      return data;
      //return nodesdata.result_nodes;
    }

  let activeId;
 let selectedIssueSeverity = NaN;

 const selectIssues = (severity) => {
  let filteredIssues = issues.filter(i => {
    let ts = !!i.n1 ? ~~i.n1 : 0;
    return ts == severity;
  });
  console.log(JSON.stringify("filteredIssues",filteredIssues));
  return filteredIssues;
 };

 const selectIssueSeverity = (s) => {selectedIssueSeverity = s; console.log("selectIssueSeverity",s)};
 //~~ efficiently truncates the decimal part of the variable, turning it into an integer.
 const getIssueCatData = (data) => {
  return [
  {label: "Critical", num: ~~data.issueCategories.critical, sevSelect: 10},
  {label: "High", num: ~~data.issueCategories.high, sevSelect: 9},
  {label: "Medium", num: ~~data.issueCategories.medium, sevSelect: 8},
  {label: "Low", num: ~~data.issueCategories.low, sevSelect: 7},
  {label: "Information", num: ~~data.issueCategories.information, sevSelect: 6},
  {label: "Investigating", num: ~~data.issueCategories.investigating, sevSelect: 5},
  {label: "Closed", num: ~~data.issueCategories.closed, sevSelect: 4},
  {label: "New", num: ~~data.issueCategories.new, sevSelect: 0}
  ];

 };

const getIssueCatClass = (label,num) => {
    let _label = (num > 0) ? label : 'Disabled';
    return `cat cat_${_label}`;
    };

</script>

<style>
.cat {
  color: white;
  padding: 0.2em;
  border: 1px solid white;
  border-radius: 5px;
}

.cat_Critical {
  background-color: #8f2d56;
}

.cat_High {
  background-color: #d81159;
}

.cat_Medium {
  background-color: #ffbc42;
}

.cat_Low {
  background-color: #73d2de;
}

.cat_Information {
  background-color: #6096ba;
}

.cat_Investigating {
  background-color: #4aabaf;
}

.cat_Closed {
  background-color: #58586b;
}


.cat_New {
  background-color: #218380;
}

.cat_Disabled {
  background-color: #cdcdcd;
}

</style>

<div>
	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>
	<!--AsyncWrapper awaitable={fetcher} let:dataFromAwaitable-->

		<!-- in the AsyncWrapper tag, the let:dataFromAwaitable should be written as-is -->
		<!-- dataFromAwaitable is a reactive variable containing the return value from yourAsyncFunctionGoesHere() -->
<h1>Project {dataFromAwaitable.thisproject.s1  || "[Untitled]"}</h1>

<SecSpacer hclass="40"/>

<h2>Issues</h2>
<!--
<Button on:click={() => selectIssueSeverity(10)}>
<span>Critical</span>&nbsp;&nbsp;
<h3>{~~dataFromAwaitable.issueCategories.critical}</h3>
</Button>

<Button on:click={() => selectIssueSeverity(9)}>
<span>High</span>&nbsp;&nbsp;
<h3>{~~dataFromAwaitable.issueCategories.high}</h3>
</Button>

<Button on:click={() => selectIssueSeverity(8)}>
<span>Medium</span>&nbsp;&nbsp;
<h3>{~~dataFromAwaitable.issueCategories.medium}</h3>
</Button>

<Button on:click={() => selectIssueSeverity(7)}>
<span>Low</span>&nbsp;&nbsp;
<h3>{~~dataFromAwaitable.issueCategories.low}</h3>
</Button>

<Button on:click={() => selectIssueSeverity(6)}>
<span>Information</span>&nbsp;&nbsp;
<h3>{~~dataFromAwaitable.issueCategories.information}</h3>
</Button>

<Button on:click={() => selectIssueSeverity(0)}>
<span>New</span>&nbsp;&nbsp;
<h3>{~~dataFromAwaitable.issueCategories.new}</h3>
</Button>
-->
<Grid><Row>
{#each getIssueCatData(dataFromAwaitable) as cd}
<Column sm={2} md={2} lg={2}>
<ClickableTile
  disabled={cd.num == 0}
  on:click={(e) => {
    e.preventDefault();
    if(cd.num > 0)
      selectIssueSeverity(cd.sevSelect);
  }}
>
<div class={getIssueCatClass(cd.label,cd.num)}>{cd.label}</div>&nbsp;&nbsp;
<h3>{cd.num}</h3>
</ClickableTile>
</Column>
{/each}


</Row></Grid>


<SecSpacer hclass="40"/>

<h2>Projects</h2>
<!--tree-->
<TreeView
  labelText="Hierarchy of Projects and Sub Projects"
  children={[dataFromAwaitable.thisproject]}
  bind:activeId
  bind:expandedIds
  on:select={() => navigate(`/ui/usr/proj-view/${activeId}`)}
/>

	</AsyncWrapper>

{#key selectedIssueSeverity}
<SecSpacer hclass="40"/>

<IssuesList issues={selectIssues(selectedIssueSeverity)} />
{/key}

</div>
