<script>

  import { Button, TextInput } from "carbon-components-svelte";
import ToastNotify from "./ToastNotify.svelte";

    export let adm;
    export let orgAD;

    let notification = null;

    let id = 'team_id';
    let title = '';

    let clickedCreate = async () => {
      let teamdata = {}
      teamdata.id = id;
      teamdata.s1 = title;
      let responsedata = null;
      let errorMessage = "";
      try {
        responsedata = await adm.addTeam(orgAD, teamdata);
      } catch (err) {
        errorMessage = err.message;
      }
      console.log(responsedata);
      if(!!responsedata && !!responsedata.created_nodes) {
        notification = '+'+JSON.stringify(responsedata);
      } else {
        notification = `Failed to create team (${errorMessage})`;
      }

    }

</script>

<style>
</style>

<ToastNotify {notification}/>

	<h1>Create Team</h1>

<TextInput bind:value={id} labelText="Team ID" placeholder="Enter Team ID..." />
<br/>
<TextInput bind:value={title} labelText="Team Name" placeholder="Enter Team Name..." />
<br/>
<Button on:click={clickedCreate}>Create</Button>

