<script>

 import { DataTable } from "carbon-components-svelte";
  import { Button, TextInput } from "carbon-components-svelte";
	import AsyncWrapper from './AsyncWrapper.svelte';
  import { Link } from "svelte-navigator";

    export let sys;

    let fetchData = async () => {
      let orgdata = await sys.getOrganisations("adm");
      return orgdata.result_nodes;
    }

</script>

<style>
</style>



<div>
	<AsyncWrapper awaitable={() => fetchData()} let:dataFromAwaitable>

		<!-- in the AsyncWrapper tag, the let:dataFromAwaitable should be written as-is -->
		<!-- dataFromAwaitable is a reactive variable containing the return value from yourAsyncFunctionGoesHere() -->
<h1>List Organisations</h1>

<DataTable
  headers={[
    { key: "uid", value: "UID" },
    { key: "id", value: "Org ID" },
    { key: "ad", value: "Manage" }
    ]}
  rows={dataFromAwaitable}
  >

  <svelte:fragment slot="cell" let:row let:cell>
    {#if cell.key === "ad"}
      <Link
        to={"/ui/adm/org-teams/"+cell.value}
        >Manage Teams</Link>
    {:else}
      {cell.value}
    {/if}
  </svelte:fragment>
</DataTable>

	</AsyncWrapper>
</div>

