<script>
 import { InlineNotification } from "carbon-components-svelte";
export let notification;
</script>

{#if !!notification && notification.startsWith("+")}

<InlineNotification
  kind="success"
  title="Success"
  subtitle={notification.substring(1)}
  on:close={(e) => {
    notification = null;
  }}
/>

{:else if !!notification}

<InlineNotification
  kind="error"
  title="Error:"
  subtitle={notification}
  on:close={(e) => {
    notification = null;
  }}
/>

{/if}
